.ui-tabs {
  position: relative;
  padding: .2em;

  &-nav {
    margin: 0;
    padding: .2em .2em 0;

    li {
      list-style: none;
      float: left;
      position: relative;
      top: 0;
      margin: 1px .2em 0 0;
      border-bottom-width: 0;
      padding: 0;
      white-space: nowrap;

      &.ui-tabs-active {
        margin-bottom: -1px;
        padding-bottom: 1px;
      }

      &.ui-tabs-active .ui-tabs-anchor,
      &.ui-state-disabled .ui-tabs-anchor,
      &.ui-tabs-loading .ui-tabs-anchor {
        cursor: text;
      }
    }

    .ui-tabs-anchor {
      float: left;
      padding: .5em 1em;
      text-decoration: none;
    }
  }

  &-collapsible .ui-tabs-nav li.ui-tabs-active .ui-tabs-anchor {
    cursor: pointer;
  }

  &-panel {
    display: block;
    border-width: 0;
    padding: 1em 1.4em;
    background: none;
  }
}
